/* 
PROSE BLOCK
Default value for spacing between text paragraphs.
*/
.prose {
  --flow-space: var(--space-m);
}

/* 
GLOBAL STYLES
Site-wide styles.
*/
/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Prevent font size inflation */
html {
  -moz-text-size-adjust: none;
  -webkit-text-size-adjust: none;
  text-size-adjust: none;
}

/* Remove default margin in favour of better control in authored CSS */
body, h1, h2, h3, h4, p,
figure, blockquote, dl, dd {
  margin-block-end: 0;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list] {
  list-style: none;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  line-height: 1.5;
}

/* Set shorter line heights on headings and interactive elements */
h1, h2, h3, h4,
button, input, label {
  line-height: 1.1;
}

/* Balance text wrapping on headings */
h1, h2,
h3, h4 {
  text-wrap: balance;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
  color: currentColor;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input, button,
textarea, select {
  font-family: inherit;
  font-size: inherit;
}

/* Make sure textareas without a rows attribute are not tiny */
textarea:not([rows]) {
  min-height: 10em;
}

/* Anything that has been anchored to should have extra scroll margin */
:target {
  scroll-margin-block: 5ex;
}

/* 
REGION UTILITY
Consistent block padding for page sections.
*/
.region-small {
  padding-block: var(--region-space-small, var(--space-m-l));
}

.region-normal {
  padding-block: var(--region-space-normal, var(--space-l-xl));
}

.region {
  padding-block: var(--region-space, var(--space-xl-2xl));
}

.region-bottom {
  padding-block-end: var(--region-space, var(--space-xl-2xl));
}

/* 
FLOW UTILITY
Spacing value between content blocks.
*/
.flow > * + * {
  margin-top: var(--flow-space, 1em);
}

.flow-large > * + * {
  margin-top: var(--flow-space-large, 1em);
}

/* 
WRAPPER UTILITY
Settings for the overall content width.
*/
.wrapper {
  margin-inline: auto;
  max-width: clamp(16rem, var(--wrapper-max-width, 100vw), 80rem);
  padding-left: var(--gutter);
  padding-right: var(--gutter);
  position: relative;
}

/* 
VISUALLY HIDEN UTILITY
Modern way of properly hiding elements.
*/
.visually-hidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: 0;
  margin: 0;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
  white-space: nowrap;
}

/* 
BORDER RADIUS UTILITY
Values for consistent border radius.
*/
:root {
  --radius-xs: 3px;
  --radius-small: 8px;
  --radius-normal: 16px;
  --radius-large: 48px;
}

.rounded {
  border-radius: var(--radius-normal);
}